import commonIndex from './index.common'

import basePlugins from './basePlugins'
import auth from './auth/link'
import call from './call/oldLink' // TODO: Update when new Link is done.
import callHistory from './callHistory'
import clickToCall from './clickToCall'
import connectivity from './connectivity/link'
import messaging from './messaging/link'
import mwi from './mwi/link'
import notifications from './notifications/link'
import presence from './presence/link'
import sipEvents from './sipEvents'
import users from './users/link'
import './docs/docs'

import createCodecRemover from 'fcs/src/js/sdp/codecRemover'

const defaultPlugins = [
  ...basePlugins,
  { name: 'authentication', fn: auth },
  { name: 'call', fn: call },
  { name: 'callHistory', fn: callHistory },
  { name: 'clickToCall', fn: clickToCall },
  { name: 'connectivity', fn: connectivity },
  { name: 'messaging', fn: messaging },
  { name: 'mwi', fn: mwi },
  { name: 'notifications', fn: notifications },
  { name: 'presence', fn: presence },
  { name: 'sipEvents', fn: sipEvents },
  { name: 'users', fn: users }
]

function root (options = {}, plugins = []) {
  return commonIndex(options, [...defaultPlugins, ...plugins])
}

// Alias 'create' to be equal to the root function
root.create = root

root.sdpHandlers = {
  createCodecRemover
}

// Export this way as a work-around, so it can be used as `<export>();`.
// See: https://github.com/webpack/webpack/issues/706
module.exports = root
